<template>
  <div class="bike-list">
   <div v-if="showRentForm" class="rentForm">
        <div class="text-align-left"><b>Selected Bike:</b></div>
      <div class="container">
        <div class="bike-detail">Style: {{ currentBikeStyle }}</div>
        <div class="bike-detail">Size: {{ currentBikeSize }}</div>
        <div class="bike-detail">Color: {{ currentBikeColor }}</div>
        <div class="bike-detail"><b>Total Price: $5</b></div>
      </div>
      <div class="text-align-left"><b>Enter Payment Information:</b></div>
      <div class="container">
        <input class="long-input margin-right" v-model="creditName" placeholder="Name">
        <input class="long-input" v-model="creditCard" placeholder="Credit Card Number">
      </div>
      <div class="container">
        <input class="short-input margin-right" v-model="expiryDate" placeholder="Expiry Date">
        <input class="short-input margin-right" v-model="cvv" placeholder="CVV">
        <button class="bike-btn short-input margin-right" @click="cancelRent()">Cancel</button>
        <button class="bike-btn short-input" @click="rentBike()">Submit</button>
      </div>
      
    </div>
  <table v-else>
    <thead>
      <tr>
        <th class="bike-headers">Style</th>
        <th class="bike-headers">Size</th>
        <th class="bike-headers">Color</th>
        <th class="bike-headers">Maintenance</th>
        <th class="bike-headers"></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(bike,index) in bikes" :key="bike._id" :style="{backgroundColor: index%2 !== 0 ? '#F4F4F4' : 'white'}">
        <td class="bike-body">{{ bike.style }}</td>
        <td class="bike-body">{{ bike.size }}</td>
        <td class="bike-body">{{ bike.color }}</td>
        <td class="bike-body">{{ bike.maintenance_issues }}</td>
        <td class="bike-body" @click="openRentForm(bike._id, bike.style, bike.size, bike.color, bike.maintenance_issues)"><button class="bike-btn rent-btn-width">Rent</button></td>
      </tr>
    </tbody>
  </table>
  </div>
</template>

<script setup>
    import { ref, onMounted } from "vue";
    import router from '@/router';
    import {globalPostConnect} from '@/global.js';
    import {globalGetConnect} from '@/global.js';

    const stationName = router.currentRoute.value.params.stationName
    const bikes = ref([]);
    const currentStartTime = ref();
    const currentSessionLength = ref();
    const currentSessionId = ref();

    async function getAllBikes() {
      const baseUrl = "api/locations/bikes/"+ stationName
      bikes.value = (await globalGetConnect(baseUrl)).data
    }

    async function rentBikeRequest() {
      const baseUrl = "api/rentals/rent/" + stationName + "/" + currentBikeId.value
      const responseData = (await globalPostConnect(baseUrl)).data

      currentSessionId.value = responseData.session_id;
      const dateObj = new Date(responseData.start_time);
      currentStartTime.value = dateObj.toLocaleTimeString();
      currentSessionLength.value = responseData.session_length;
    }

    onMounted(async () => {
      await getAllBikes();
    });

const showRentForm = ref(false);
const currentBikeId = ref();
const currentBikeStyle = ref();
const currentBikeSize = ref();
const currentBikeColor = ref();
const currentBikeMaintenanceIssues = ref();

const creditName = ref();
const creditCard = ref();
const expiryDate = ref();
const cvv = ref();

function openRentForm(bikeId, bikeStyle, bikeSize, bikeColor, maintenanceIssues) {
    currentBikeId.value = bikeId;
    currentBikeStyle.value = bikeStyle;
    currentBikeSize.value = bikeSize;
    currentBikeColor.value = bikeColor;
    currentBikeMaintenanceIssues.value = maintenanceIssues;

    console.log("Current Bike Id: " + currentBikeId.value)

    showRentForm.value = true;
}

function cancelRent() {
    showRentForm.value = false;
}

async function rentBike() {
  await rentBikeRequest();
    router.push({
        path: '/session/' + currentSessionId.value + "/" + currentStartTime.value + "/" + currentSessionLength.value + "/" + currentBikeId.value
    })
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
    display: flex;
    margin: 10px 0px;
    width: 100%;
}

.bike-detail {
    width: 25%;
    text-align: left;
}

.long-input {
    width: 295px;
}

.short-input {
    width: 142.5px;
}

table {
    width: 60%;
    margin: 0% 20%;
    padding: auto;
    
}

.bike-headers {
    border-bottom: 1px solid black;
    width: 20%;
}

.bike-body {
    height: 50px;
    border-bottom: 1px solid lightgray;
}

.bike-btn {
  border-radius: 10px;
  background-color: #a0c8d7;
  border: 1px solid black;
  height: 30px;
  cursor: pointer;
}

.rent-btn-width {
    width: 75px;
}

.rentForm {
    border: 2px black solid;
    border-radius: 10px;
    width: 40%;
    margin: 3% 30%;
}
</style>